import React from 'react';
import UpcomingTaskInnerComponent from '../HomePage/upcoming_task_inner_component'
import { Table, Button, Container, Row, Col,OverlayTrigger,Tooltip } from 'react-bootstrap';
import {withRouter} from 'react-router-dom'

class UpcomingTask extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            status : false
        }
    }

    handleAddTask = () => {
        if(window.localStorage.getItem('userType') == 1){
            this.props.history.push({
                pathname : '/emp_add_task',
                state: {
                    type : 'upcoming',
                    name: this.props.location.state.name,
                    user_id : this.props.location.state.user_id
                }
            })
        }
        if(window.localStorage.getItem('userType') == 2){
            this.props.history.push({
                pathname : '/add_task',
                state: {
                    type : 'upcoming'
                }
            })
        }
    }

    handleEditStatus = (status) => {
        if(status){
            this.setState({
                status : true
            })
        }else{
            this.setState({
                status : false
            })
        }
    }

    handleEdit = () => {
        this.props.history.push({
            pathname : "/edit_task",
            state: {
                type : 'upcoming'
            }
        })
    }

    render(){
        return (
            <div className="mt-5 mb-5">
                <Container className="border">
                        <h4 className="float-left mt-3"><b>Upcoming Tasks</b></h4>
                    <div className="text-center mt-3 mb-5">
                        <Button variant="primary" onClick={this.handleAddTask} type="button">Add New Task</Button>
                        {
                            window.localStorage.getItem('userType') == 2 &&
                            (this.state.status ?
                            <Button className="btn-success float-right" onClick={this.handleEdit} type="button" >Edit</Button>
                            :
                            <OverlayTrigger 
                            overlay={
                                <Tooltip id={`tooltip-top`}>
                                    For Edit you have to add <strong>New Tasks</strong>.
                                </Tooltip>
                            }>
                                <Button variant="secondary" className="float-right" type="button" >Edit</Button>
                            </OverlayTrigger>)
                        }
                    </div>
                    <div className="ml-5 mr-5">
                        {
                            window.localStorage.getItem('userType') == 1 ?
                            <UpcomingTaskInnerComponent id={this.props.location.state.user_id} handleEditStatus={(status)=>this.handleEditStatus(status)}/>
                            :
                            <UpcomingTaskInnerComponent handleEditStatus={(status)=>this.handleEditStatus(status)}/>
                        }
                    </div>
                </Container>
            </div>
        )
    }
}


export default withRouter(UpcomingTask);