import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Form } from 'react-bootstrap';
import {Link, withRouter} from 'react-router-dom'
import { toast } from 'react-toastify'
import services from '../services';
// import 'react-toastify/dist/ReactToastify.css';
// import backgroundImage from './logHour.jpg'
// import Login from './Login/login'

class ForgotPassword extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      email : '',
      status : false
    }
  }

  handleFields = (e) => {
    if(e.target.name == 'email'){
      this.setState({
        email : e.target.value
      })
    }
  }

  handleForget = async () => {
    let reg = /^([A-Za-z0-9_\-\.])+\@enukesoftware.com$/;

    if(this.state.email === ''){
        toast.error("Email Required");
    }else if (this.state.email !== '' && !reg.test(this.state.email)) {
        toast.error("Invalid Email")
    }else{
        let _r = await services.auth.resetEmailVerify({email : this.state.email});
        if(_r.success){
            this.setState({
                status : true
            })
        }else{
            toast.error("User not exist.")
        }
    }
  }

  render(){
    console.log(".........",this.state.status)
    return (
      <div>
        {
            !this.state.status ?
            <Form> 
                <Form.Group controlId="formBasicEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control type="email" name='email' value={this.state.email} onChange={this.handleFields} placeholder="Registered Email"/>
                </Form.Group>
                <Button variant="primary" onClick={this.handleForget} type="button">
                    Submit
                </Button>
                <Link className="float-right mt-2" to={'/login'}>Login</Link>
            </Form>
            :
            <div className="text-center mt-5 text-success">
                <h3>Reset Link is Succesfully Sent to your registered Email.</h3>
                <h2>Simply Check and Reset your Password</h2>
                <h1><b>Thank You!</b></h1>
                <b>Back To </b><Link className="mt-5 mr-2" to="/login"><b>Login</b></Link><b>Page</b>
            </div>
        }
      </div>
    )
  }
}

export default withRouter(ForgotPassword);
