import React from 'react';
import {Table} from 'react-bootstrap'
import { Link } from 'react-router-dom';
import services from '../../services';


const column = [
    {
        name : "Name",
        key : "name"
    },
    {
        name : "From",
        key : "from"
    },
    {
        name : "To",
        key : "to"
    },
    {
        name : "No of Day's",
        key : "days"
    }
]

class TableEmployeeOnLeave extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            data : []
        }
    }

    componentDidMount() {
        this.getOnLeaveData();
    }

    getOnLeaveData = async () => {
        let resp = await services.admin.getTLDashboardData('onLeave');
        if(resp.success){
            this.setState({
                data : resp.data
            })
        }
        console.log("hddddddddddddd",resp)
    }

    render(){
        return(
            <div>
                <Table triped bordered hover>
                    <thead className="bg-secondary text-white">
                        <tr>
                            {
                                column.map((_c,index)=>
                                    <th className="text-center">{_c.name}</th>
                                )
                            }
                        </tr>
                    </thead>
                    {
                        this.state.data.length > 0 ?
                        <tbody>
                        {
                            this.state.data.map((_r,index)=>
                            <tr key={`row${index}`} className="text-center">
                                <td>
                                    <Link to={{pathname:'/emp_dashboard',state:{ user_id : _r.user_leave_userid , name:_r.user_name}}}>{_r.user_name}</Link>
                                    {
                                        _r.user_pending_task ?
                                        <button type="button" className="btn btn-sm btn-danger ml-3">{_r.user_pending_task} Pending this week</button>
                                        :
                                        <button type="button" className="btn btn-sm btn-success ml-3">All Done this week</button>
                                    }
                                </td>
                                <td>{_r.user_leave_start_date}</td>
                                <td>{_r.user_leave_end_date}</td>
                                <td>{_r.user_leave_leave_days}</td>
                            </tr>
                            )
                            }
                        </tbody>
                        :
                        <tbody>
                            <tr>
                                <td colSpan='4' className="text-center text-secondary">There is No Employee on Leave.</td>
                            </tr>
                        </tbody>
                    }
                </Table>
            </div>
        )
    }
}


export default TableEmployeeOnLeave;