import React from 'react';
import LoginPage from '../Pages/login_page'
import SignUpPage from '../Pages/signup_page'
import axios from 'axios'
// import App from './App'
import ForgetPasswordPage from '../Pages/forgot_page'
import ResetPassword from '../Pages/reset_password_page'
import AccountVerify from '../Pages/emailLinkVerify'
// import Dashboard from './Pages/HomePage'
import {Switch,Route,withRouter, Redirect} from 'react-router-dom'

// axios.defaults.baseURL = 'http://workhourslog.com/api/';

class AuthSwitch extends React.Component{
  constructor(props){
    super(props);
  }
  

  componentDidMount() {
  }

  render(){
    console.log(this.props)
    return (
      <Switch>
        <Route exact path='/' render={()=>(<Redirect to="/login"/>)} />
        <Route exact path='/login' exact component={LoginPage} />
        <Route path='/forgot_password' component={ForgetPasswordPage} />
        <Route path='/signup' component={SignUpPage} />
        <Route path='/reset_password' component={ResetPassword} />
        <Route pat='/account_verify' component={AccountVerify} />
      </Switch>
    )
  }
}

export default withRouter(AuthSwitch);
