import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Form } from 'react-bootstrap';
import {Link, withRouter} from 'react-router-dom'
import { toast } from 'react-toastify'
import services from '../services';
// import 'react-toastify/dist/ReactToastify.css';
// import backgroundImage from './logHour.jpg'
// import Login from './Login/login'

class ResetPassword extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      Password : '',
      status: false
    }
  }

  componentDidMount() {
    window.localStorage.removeItem('reset_token')
    let data = window.location.search.substr(1);
    window.localStorage.setItem('reset_token',data)
  }

  handleFields = (e) => {
    if(e.target.name == 'password'){
      this.setState({
        Password : e.target.value
      })
    }
  }

  handleReset = async () => {
    if(this.state.Password === ''){
        toast.error("Password Required");
    }else{
      let _r = await services.auth.resetPassword({password : this.state.Password});
      if(_r.success){
        toast.success('Password change successfully.')
        window.localStorage.removeItem('reset_token')
        this.props.history.push({
          pathname: '/login'
        })
      }else{
        this.setState({
          status : true
        })
      }
    }
  }

  render(){
    return (
      <div>
        {
          this.state.status ?
          <div className="text-center mt-5">
            <h2 className="text-danger"><b>Sorry! You are not authorized</b></h2>
            <b>Go Back To</b><Link to='/login' className="ml-2">Login</Link>
          </div>
          :
          <Form> 
            <Form.Group controlId="formBasicPassword">
                <Form.Label>New Password</Form.Label>
                <Form.Control type="password" name='password' value={this.state.Password} onChange={this.handleFields} placeholder="New Password" />
            </Form.Group>
            <Button variant="primary" onClick={this.handleReset} type="button">
                Reset
            </Button>
        </Form>
        }
      </div>
    )
  }
}

export default withRouter(ResetPassword);
