import React, { Fragment } from 'react';
import Table from 'react-bootstrap/Table'
import { Dropdown, DropdownButton } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import services from '../../services';

const column = [
    {
        name: "Task",
        key: "task_name"
    },
    {
        name: "Time Spent",
        key: "time_spent"
    },
    {
        name: "Estimated Time",
        key: "estimated_time"
    }
]

class TodayTaskInnerComponent extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            data : []
        }
    }

    componentDidMount(){
        if(window.localStorage.getItem('userType')==1){
            this.getTodayTaskWithID(this.props.id);
        }
        if(window.localStorage.getItem('userType')==2){
            this.getTodayTask();
        }
    }

    getTodayTask = async () => {
        let _r = await services.dashboard.getTask('today');
        console.log("Response",_r)
        if(_r.success){
            this.setState({
                data : _r.data
            },()=>{
                if(this.state.data.length == 0){
                    this.props.handleEditStatus(false);
                }else{
                    this.props.handleEditStatus(true);
                }
            })
        }
    }

    getTodayTaskWithID = async (id) => {
        let _r = await services.admin.getTaskWithUserId('today',id);
        if(_r.success){
            this.setState({
                data : _r.data
            },()=>{
                if(this.state.data.length == 0){
                    this.props.handleEditStatus(false);
                }else{
                    this.props.handleEditStatus(true);
                }
            })
        }
    }

    render() {
        // onClick={(e) => { e.preventDefault(); e.stopPropagation(); this.confirmDelete(_r.id) }}
        return (
            <Fragment>
                <Table striped bordered hover>
                    <thead className="bg-secondary text-white">
                        <tr>
                            {
                                column.map((_c)=>(
                                <th className="text-center">{_c.name}</th>
                                ))
                            }
                            {/* <th className="text-center">Action</th> */}
                        </tr>
                    </thead>
                    {
                        this.state.data.length > 0 ?
                        <tbody>
                        {
                            this.state.data.map((row,index)=>(
                                    row.completed ?
                                    <tr key={`row${index}`} className="text-center">
                                        {
                                            column.map((_c,i)=>(
                                                _c.key === 'estimated_time' ?
                                                <td style={{textDecoration:"line-through"}} >{`${row[_c.key]} ${row.estimated_time_value}`}</td>
                                                :
                                                _c.key === 'time_spent' ?
                                                <td style={{textDecoration:"line-through"}} >{`${row[_c.key]} ${row.time_spent_value}`}</td>
                                                :
                                                <td style={{textDecoration:"line-through"}} >{row[_c.key]}</td>
                                            ))
                                        }
                                    </tr>
                                    :
                                    <tr key={`row${index}`} className="text-center">
                                        {
                                            column.map((_c,i)=>(
                                                _c.key === 'estimated_time' ?
                                                <td>{`${row[_c.key]} ${row.estimated_time_value}`}</td>
                                                :
                                                _c.key === 'time_spent' ?
                                                <td>{`${row[_c.key]} ${row.time_spent_value}`}</td>
                                                :
                                                <td>{row[_c.key]}</td>
                                            ))
                                        }
                                    </tr>
                                ))
                            }
                        </tbody>
                    :
                            <tbody>
                                <tr className="text-center text-secondary">
                                    <td colSpan="3">Nothing is here. Please <b>ADD NEW TASK</b></td>
                                </tr>
                            </tbody>
                    }
                </Table>
            </Fragment>
        )
    }
}

export default TodayTaskInnerComponent;