import React from 'react';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
var moment = require('moment');
// moment().format();

class TomorrowComingStatus extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            selectedOption : 'comingYes',
            tom_date : new Date()
        }
    }

    componentDidMount() {
        let date = new Date();
        if(this.props.props.location.state.type === 'tomorrow'){
                let tomorrow = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 1));
                this.setState({
                    tom_date : tomorrow
                },()=>{
                this.props.handleComingTomorrowStatus(this.state.selectedOption)
                this.props.handleComingTomorrowDate(this.state.tom_date)
            })
        }else if(this.props.props.location.state.type === 'upcoming'){
                let upcoming = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 2));
                this.setState({
                    tom_date : upcoming
                },()=>{
                this.props.handleComingTomorrowStatus(this.state.selectedOption)
                this.props.handleComingTomorrowDate(this.state.tom_date)
            })
        }else{
                this.props.handleComingTomorrowStatus(this.state.selectedOption)
                this.props.handleComingTomorrowDate(this.state.tom_date)
            }
    }

    handleRadio = (e) => {
        if(e.target.value === 'comingYes'){
            this.setState({
                selectedOption : e.target.value,
                tom_date : new Date()
            },()=>{
                this.props.handleComingTomorrowStatus(this.state.selectedOption)
                this.props.handleComingTomorrowDate(this.state.tom_date)})
        }else{
            this.setState({
                selectedOption : e.target.value
            },()=>this.props.handleComingTomorrowStatus(this.state.selectedOption))
        }
    }

    handleChange =(e) => {
        // console.log("Date",e)
        this.setState({
            tom_date : e
        },()=>this.props.handleComingTomorrowDate(this.state.tom_date))
    }

    render(){
        console.log(this.props)
        return (
            <div>
                <h3>Coming Tomorrow ?</h3>
                <div>
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-2 ml-5" type="radio" value="comingYes" checked={this.state.selectedOption === 'comingYes'} />
                        <label className="ml-2">Yes</label>    
                    </span>
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-2 ml-5" type="radio" value="comingNo" checked={this.state.selectedOption === 'comingNo'} />
                        <label className="ml-2">No</label>
                    </span>
                </div>
                {
                    this.state.selectedOption === 'comingNo' && 
                    <div className="mt-3 ml-5">
                        <b>Until :-</b>
                        <DatePicker
                        className="ml-2 text-center"
                        dateFormat="dd/MM/yyyy"
                        selected={this.state.tom_date}
                        onChange={this.handleChange}
                        minDate={new Date()}
                        />
                    </div>
                }
            </div>
        )
    }
}

export default TomorrowComingStatus;