import React from 'react';
import services from '../services';
import {Form} from 'react-bootstrap'
import { Link } from 'react-router-dom';

class LinkVerifyPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            status : false
        }
    }

    componentDidMount(){
        window.localStorage.removeItem('account_verify_token')
        let data = window.location.search.substr(1);
        window.localStorage.setItem('account_verify_token',data)
        this.accountVerification();
    }

    accountVerification = async () => {
        let resp = await services.auth.accountVerification();
        if(resp.success){
            this.setState({
                status : true
            })
        }
    }

    render(){
        return (
            <div style={{marginLeft : "35%" ,marginTop:"10%", width: "25%"}}>
                {
                    this.state.status ?
                    <div>
                        <Form>
                            <Form.Text><h2 className='text-center text-primary'><b>Enuke EMS Portal</b></h2></Form.Text>
                        </Form>
                        <div className="text-center text-success">
                            <h4>Your account successfully verified.</h4>
                            <h2>Thank You!</h2>
                        </div>
                        <div className="mt-4 text-center">
                            <span className="text-success"><b>Click here for </b></span><Link to={'/login'}>Login</Link>
                        </div>
                    </div>
                    :
                    <Form>
                        <Form.Text><h2 className='text-center'><b>404- Not Found</b></h2></Form.Text>
                    </Form>
                }
            </div>
        )
    }
}

export default LinkVerifyPage;