import React from 'react';
import CommonNavbar from '../components/common/navbar'
// import TomorrowEditComingStatus from '../components/EditTask/tomorrowEditStatus'
import EditNextTask from '../components/EditTask/editNextTask'
import ColoredLine from '../components/common/horizontalLine'
import { Button } from 'react-bootstrap';
import { withRouter } from 'react-router-dom'
import services from '../services';
import DatePicker from "react-datepicker";
var moment = require('moment');


class EditTaskPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            statusTomorrow : true,
            dateTomorrow : '',
            data : [],
            comingTomorrow : {},
            selectedOption : 'comingYes',
            tom_date : new Date()
        }
     }

    componentDidMount() {
        if(this.props.location.state.type === 'today'){
            this.getEditTask('today');
        }
        if(this.props.location.state.type === 'tomorrow'){
            this.getEditTask('tomorrow');
        }
        if(this.props.location.state.type === 'upcoming'){
            this.getEditTask('upcoming');
        }
    }

    getEditTask = async (type) => {
        let _r = await services.task.getEditTask(type);
        
        if(_r.success){
            let date = new Date()
            this.setState({
                data : _r.data,
                comingTomorrow : _r.user_leave,
                tom_date : date,
                selectedOption : ''
            },()=>this.handleComingStatus())
        }
        console.log('eeeeeeeeeee',this.state)
    }

    handleComingStatus = () => {
        if(this.state.selectedOption === 'comingYes'){
            this.setState({
                statusTomorrow : true,
                dateTomorrow : ''
            })
        }else if(this.state.selectedOption === 'comingNo'){
            this.setState({
                statusTomorrow : false,
                dateTomorrow : this.state.tom_date
            })
        }else{
            this.setState({
                selectedOption : ''
            })
        }
    }

    handleRadio = (e) => {
        this.setState({
            selectedOption : e.target.value
        },()=>this.handleComingStatus())
    }

    handleChange =(e) => {
        // console.log("Date",e)
        this.setState({
            tom_date : e
        },()=>this.handleComingStatus())
    }

    render(){
        console.log("kkkkkkkkk",this.state)
        return (
            <div>
                <CommonNavbar name="Edit Task"/>
                <div className="text-center mt-2"><h2><b>Edit Task</b></h2></div>
                <div className="mt-2">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="ml-5 mt-4">
                    <h3>Coming Tomorrow ?</h3>
                    <div>
                        <span class="radio-inline">
                            <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingYes" checked={this.state.selectedOption === 'comingYes'} />
                            <label className="ml-2">Yes</label>    
                        </span>
                        <span class="radio-inline">
                            <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingNo" checked={this.state.selectedOption === 'comingNo'} />
                            <label className="ml-2">No</label>
                        </span>
                    </div>
                    {
                        this.state.selectedOption === 'comingNo' && 
                        <div className="mt-3 ml-5">
                            <b>Until :-</b>
                            <DatePicker
                            className="ml-2 text-center"
                            dateFormat="dd/MM/yyyy"
                            selected={this.state.tom_date}
                            onChange={this.handleChange}
                            minDate={new Date()}
                            />
                        </div>
                    }
                </div>
                <div className="mt-5">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="mt-4">
                    <EditNextTask selectedOption={this.state.selectedOption} comingTom={this.state.statusTomorrow} comingTomDate={this.state.dateTomorrow} props={this.props} data={this.state.data}/>
                </div>
            </div>
        )
    }
}

export default withRouter(EditTaskPage);