import React from 'react';
import TodayTask from '../components/HomePage/todays_task'
import UpcomingTask from '../components/HomePage/upcoming_task'
import TomorrowTask from '../components/HomePage/tomorrow_task'
import { Button } from 'react-bootstrap';
import CommonNavbar from  '../components/common/navbar'
import CommonTLNavbar from '../components/common/tl_navbar'
// import { Table } from 'react-bootstrap';


class HomePage extends React.Component{
    render(){
        console.log("iiiiiiiiiiiiiiiiii",this.props)
        return (
                    <>
                        {
                            window.localStorage.getItem("userType") == 1 ?
                            <div>
                                <CommonTLNavbar name="Employee DashBoard" username={this.props.location.state.name} />
                                <TodayTask user_id={this.props.location.state.user_id} />
                                <TomorrowTask user_id={this.props.location.state.user_id}/>
                                <UpcomingTask user_id={this.props.location.state.user_id}/>
                            </div>
                        :
                            <div>
                                <CommonNavbar/>
                                <TodayTask />
                                <TomorrowTask />
                                <UpcomingTask />
                            </div>
                        }
                    </>
        )
    }
}


export default HomePage;