import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Form } from 'react-bootstrap';
import {Link, withRouter} from 'react-router-dom'
import { toast } from 'react-toastify'
import services from '../services';

class SignUp extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      email : '',
      password : '',
      name: '',
      contact: '',
      isVerifyLinksSend : false
    }
  }

  handleFields = (e) => {
    if(e.target.name == 'email'){
      this.setState({
        email : e.target.value
      })
    }
    if(e.target.name == 'password'){
      this.setState({
        password : e.target.value
      })
    }
    if(e.target.name == 'name'){
        this.setState({
          name : e.target.value
        })
      }
      if(e.target.name == 'contact'){
        this.setState({
          contact : e.target.value
        })
      }
      
  }

  handleSignUp = async () => {
    let reg = /^([A-Za-z0-9_\-\.])+\@enukesoftware.com$/;

    if(this.state.email === '' || this.state.password === '' || this.state.name === '' || this.state.contact === ''){
        toast.error("All Fields Required");
    }else if (this.state.email !== '' && !reg.test(this.state.email)) {
        toast.error("Enter Correct Email")
    }else{
        let data = {
          email : this.state.email,
          name : this.state.name,
          mobile_number : this.state.contact,
          password : this.state.password
        }
        let _r = await services.auth.signup(data);
        console.log("response",_r)
        if(_r.success){
          this.setState({
            isVerifyLinksSend : true
          })
        }else{
          toast.error("User already exist")
          this.setState({
            email : '',
            name: '',
            contact: '',
            password: ''
          })
        }
    }
  }

  render(){
    return (
      <div>
        {
          !this.state.isVerifyLinksSend ?
          <Form> 
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" name='email' value={this.state.email} onChange={this.handleFields} placeholder="Enter email" />
            </Form.Group>
            <Form.Group controlId="formBasicName">
              <Form.Label>Name</Form.Label>
              <Form.Control type="text" name='name' value={this.state.name} onChange={this.handleFields} placeholder="Enter Name" />
            </Form.Group>
            <Form.Group controlId="formBasicNumber">
              <Form.Label>Contact Number</Form.Label>
              <Form.Control type="number" name='contact' value={this.state.contact} onChange={this.handleFields} placeholder="Enter Mobile No. " />
            </Form.Group>
            <Form.Group controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" required name='password' value={this.state.password} onChange={this.handleFields} placeholder="Password" />
            </Form.Group>
            <Button variant="primary" onClick={this.handleSignUp} type="button">
              Sign Up
            </Button>
            <Link className="float-right mt-2" to={'/login'}>Login</Link>
          </Form>
          :
          <div className="text-center text-success mt-4">
            <h5>Verify Link is send to your Email.</h5>
            <h5>Please check and verify your account.</h5>
            <h2><b>Thank You!</b></h2>
            <Link className="mt-2" to={'/login'}><b>Login</b></Link>
          </div>
        }
      </div>
    )
  }
}

export default withRouter(SignUp);
