import React from 'react';
import axios from 'axios'
// import App from './App'
import Dashboard from '../Pages/HomePage'
import AddTaskPage from '../Pages/add_task_page'
import {Switch,Route,withRouter,Redirect} from 'react-router-dom'
import EditTaskPage from '../Pages/edit_task_page'
import Report from '../Pages/report'
import TLDashboard from '../Pages/TL_Pages/tl_dashboard_page'
import TLEmployeeList from '../Pages/TL_Pages/tl_employee_list_page'

class AppSwitch extends React.Component{
  constructor(props){
    super(props);
  }
  

  componentDidMount() {
    
  }

  render(){
    console.log(this.props)
    return (
          window.localStorage.getItem('userType') == 1 ?
            <Switch>
              <Route exact path='/' render={()=>(<Redirect to="/tl_dashboard"/>)} />
              <Route exact path='/tl_dashboard' component={TLDashboard} />
              <Route path='/emp_add_task' component={AddTaskPage} />
              <Route path='/emp_dashboard' component={Dashboard} />
              <Route path='/employee_list' component={TLEmployeeList} />
              <Route path='/report' component={Report} />
            </Switch>
          :
            <Switch>
              <Route exact path='/' render={()=>(<Redirect to="/dashboard"/>)} />
              <Route exact path='/dashboard' component={Dashboard} />
              <Route path='/add_task' component={AddTaskPage} />
              <Route path='/edit_task' component={EditTaskPage} />
            </Switch>
    )
  }
}

export default withRouter(AppSwitch);
