import React from 'react';
import CommonTLNavbar from '../components/common/tl_navbar'
import DatePicker from 'react-datepicker';
import { Table,Pagination } from 'react-bootstrap';
import moment from 'moment';
import services from '../services';
import ReactPaginate from 'react-paginate';


const column = [
    {
        name:"Employee",
        key:'employee'
    },
    {
        name:"Time Spent (Hours)",
        key:'time_spent'
    },
    {
        name:"Estimated Time (Hours)",
        key:'estimated_time'
    },
    {
        name:"Task Completed",
        key:'task_completed'
    }
]

class ReportPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            date : new Date(),
            pageCount : 0,
            activePage : 1,
            filterSelect : 'all',
            dateSelected : false,
            data : [],
            limit : 5,
            skip : 0,
            page: 0
        }
    }

    componentDidMount() {
        this.getReport();
    }

    getReport = async () => {

        let date = this.state.dateSelected ? moment(this.state.date).format('YYYY-MM-DD') : ''

        let data1 = {
            report_type : this.state.filterSelect,
            skip : this.state.skip,
            limit : this.state.limit,
            date : date
        }
        let resp = await services.admin.getReport(data1);
        // debugger;
        if(resp.success){
            console.log("klklklklk",resp)
            this.setState({
                data : resp.data.data,
                pageCount : ~~(Math.ceil(resp.data.total/this.state.limit))
            })
        }
    }

    handleFilter = (e) => {
        this.setState({
            filterSelect : e.target.value
        },()=>this.getReport())
    }

    handleChange = (e) => {
        this.setState({
            date : e
        },()=>this.getReport())
    }

    handlePagination = (item) => {
        let selected = item.selected;
        let skip = Math.ceil(selected * this.state.limit);
        this.setState({
            skip : skip,
            page : selected+ 1
        },()=>this.getReport())
    }

    handleStatus = (status) =>{
        this.setState({
            dateSelected : status
        },()=>this.getReport())
    }
    
    render(){
        console.log("fffffffffff",this.state.data)
        return(
            <div>
                <CommonTLNavbar name="Report" />
                <h2 className="mt-5 text-center"><b>Report</b></h2>
                <div className="text-center">
                    <div style={{width:"90%"}}>
                        <select style={{width:"15%"}} className="float-right custom-select" onClick={this.handleFilter}>
                            <option selected value="all">All</option>
                            <option value="onLeave">On Leave</option>
                            <option value="noTask">No Task</option>
                        </select>
                        <div className="float-right mr-3 mt-1">
                            {
                                this.state.dateSelected ?
                                <div>
                                    <img onClick={()=>this.handleStatus(false)} src={require('../img/trash-2.png')} />
                                    <DatePicker
                                        className="text-center ml-2"
                                        dateFormat="dd/MM/yyyy"
                                        selected={this.state.date}
                                        onChange={this.handleChange}
                                        minDate={new Date()} 
                                    />
                                </div>
                                :
                                <button onClick={()=>this.handleStatus(true)} type="button" className="btn btn-sm btn-primary">Select Date 
                                    <img className="ml-2" src={require('../img/calendar.png')} />
                                </button>
                            }
                        </div>
                    </div>
                </div>
                <div style={{marginTop:"8%",marginLeft:"10%"}}>
                    <div style={{width:"80%"}}>
                        <Table striped bordered hover>
                            <thead className="bg-secondary text-white">
                                <tr>
                                    {
                                        column.map((_c,index)=>(
                                            <th className="text-center"><b>{_c.name}</b></th>
                                        ))
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.data.length > 0 ?
                                    this.state.data.map((_r,index)=>
                                        <tr className="text-center" key={`row${index}`}>
                                            <td>{_r.user.user_name}</td>
                                            <td>{_r.user.total_time_spent}</td>
                                            <td>{_r.user.total_estimated_time}</td>
                                            <td>{_r.user.completed}/{_r.user.total_task}</td>
                                        </tr>
                                    )
                                    :
                                    <tr>
                                        <td colSpan="4" className="text-secondary text-center">Sorry! There is Nothing</td>
                                    </tr>
                                }
                            </tbody>
                        </Table>
                    </div>
                </div>
                <div style={{width:"80%", marginLeft:"15%" ,marginTop:"3%", marginBottom: "5%"}}>
                    <ReactPaginate
                        previousLabel={''}
                        nextLabel={''}
                        breakLabel={'...'}
                        breakClassName={'break-me'}
                        pageCount={this.state.pageCount}
                        marginPagesDisplayed={2}
                        pageRangeDisplayed={2}
                        onPageChange={this.handlePagination}
                        containerClassName={'pagination'}
                        subContainerClassName={'pages pagination'}
                        activeClassName={'active'}
                        pageClassName={'page-item'}
                        pageLinkClassName={'page-link'}
                    />
                </div>
            </div>
        )
    }
}

export default ReportPage;