import React from 'react';
import CommonNavbar from '../components/common/navbar'
import TomorrowComingStatus from '../components/AddTask/comeTomorrow'
import NextTaskStatus from '../components/AddTask/nextTaskStatus'
import ColoredLine from '../components/common/horizontalLine'
import CommonTLNavbar from '../components/common/tl_navbar'
import { Button } from 'react-bootstrap';
import { withRouter } from 'react-router-dom'
var moment = require('moment');

class AddTaskPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            statusTomorrow : true,
            dateTomorrow : '',
            coming_tomorrow :  {}  
        }
     }

    handleComingTomorrowStatus = (status) => {
        console.log("data",status)
        if(status === 'comingYes'){
            this.setState({
                statusTomorrow : true
            })
        }else{
            this.setState({
                statusTomorrow : false
            })
        }
    }

    handleComingTomorrowDate = (date) => {
        console.log(".......",moment(date).format('YYYY-MM-DD'))
        let tomDate = moment(date).format('YYYY-MM-DD');
        this.setState({
            dateTomorrow : tomDate
        })
    }



    render(){
        console.log("kkkkkkkkk",this.props,window.localStorage.getItem("userType"))
        return (
            window.localStorage.getItem("userType") == 1 ?
            <div>
                <CommonTLNavbar name="Employee Add Task"/>
                <div className="text-center mt-5"><h2><b>Add New Task for {this.props.location.state.name}</b></h2></div>
                <div className="mt-2">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="ml-5 mt-4">
                    <TomorrowComingStatus handleComingTomorrowDate={(date)=>this.handleComingTomorrowDate(date)} 
                                          handleComingTomorrowStatus={(status)=>this.handleComingTomorrowStatus(status)} 
                                          props={this.props}/>
                </div>
                <div className="mt-5">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="mt-4">
                    <NextTaskStatus comingTom={this.state.statusTomorrow} comingTomDate={this.state.dateTomorrow}
                                    props={this.props} />
                </div>
            </div>
            :
            <div>
                <CommonNavbar name="Add Task"/>
                <div className="text-center mt-2"><h2><b>Add New Task</b></h2></div>
                <div className="mt-2">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="ml-5 mt-4">
                    <TomorrowComingStatus handleComingTomorrowDate={(date)=>this.handleComingTomorrowDate(date)} 
                                          handleComingTomorrowStatus={(status)=>this.handleComingTomorrowStatus(status)} 
                                          props={this.props}/>
                </div>
                <div className="mt-5">
                    <ColoredLine color="#ADD8E6" />
                </div>
                <div className="mt-4">
                    <NextTaskStatus comingTom={this.state.statusTomorrow} comingTomDate={this.state.dateTomorrow}
                                    props={this.props} />
                </div>
            </div>
        )
    }
}

export default withRouter(AddTaskPage);