import React from 'react';
import { Table, Container, Col, Row } from 'react-bootstrap';
import services from '../../services';
import { Link } from 'react-router-dom';


class TableNoTaskEmployees extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            data : [],
            evenData : [],
            oddData : []
        }
    }

    componentDidMount() {
        this.getNoTaskData();
    }

    getNoTaskData = async () => {
        let resp = await services.admin.getTLDashboardData('noTask');
        if(resp.success){
            this.setState({
                data : resp.data
            },()=>{
                let evenData = [...this.state.evenData];
                let oddData = [...this.state.oddData]
                for(let i=0; i<this.state.data.length ; i++){
                    if(i%2==0){
                        evenData.push(this.state.data[i])
                        this.setState({
                            evenData
                        })
                    }else{
                        oddData.push(this.state.data[i])
                        this.setState({
                            oddData
                        })
                    }
                }
            })
        }
    }

    render(){
        return (
            <Container>
                <Row>
                    <Col>
                        <Table triped bordered hover >
                            {
                                this.state.evenData.map((_r,index)=>
                                    <tr className="text-center" key={`row${index}`}>
                                        <td><Link to={{pathname:'/emp_dashboard' , state:{user_id:_r.id, name:_r.name}}} >{_r.name}</Link></td>
                                    </tr>
                                )
                            }
                        </Table>
                    </Col>
                    <Col>
                        <Table triped bordered hover >
                            {
                                this.state.oddData.map((_r,index)=>
                                    <tr className="text-center" key={`row${index}`}>
                                        <td><Link to={{pathname:'/emp_dashboard' , state:{user_id:_r.id, name:_r.name}}} >{_r.name}</Link></td>
                                    </tr>
                                )
                            }
                        </Table>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default TableNoTaskEmployees;