import React from 'react';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ColoredLine from '../common/horizontalLine'
import { Table ,Button } from 'react-bootstrap';
import {withRouter} from 'react-router-dom'
import services from '../../services';
import { toast } from 'react-toastify';
// import TaskTableComponent from '../AddTask/taskTable'
var moment = require('moment');


const column = [
    {
        name: "Task",
        key: "task"
    },
    {
        name: "Time Spent",
        key: "time_spent"
    },
    {
        name: "Estimated Time",
        key: "est_time"
    }
]
let coming_tomorrow = {}
let new_task = {}

class NextTaskStatus extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            selectOption : 'nextTaskYes',
            nextTaskDate : new Date(),
            rows : ['row'],
            data : [
                {
                    task_name: '',
                    time_spent: 0,
                    est_time: 0,
                    time_spent_value : 'hour',
                    est_time_value: 'hour',
                    isCompleted : false
                }
            ],
            isDateValid : true
        }
    }

    componentDidMount() {
        let date = new Date();
        if(this.props.props.location.state.type === 'tomorrow'){
            let tomorrow = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 1));
            this.setState({
                nextTaskDate: tomorrow
            })
        }else if(this.props.props.location.state.type === 'upcoming'){
            let upcoming = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 2));
            this.setState({
                nextTaskDate: upcoming
            })
        }else{
            let today = this.state.nextTaskDate;
            this.setState({
                nextTaskDate: today
            })
        }
    }


    handleRadio = (e) => {
        console.log(".,.,.,.,",e.target.value)
        this.setState({
            selectOption : e.target.value
        })
    }

    handleChange =(e) => {
        // console.log("Date",e)
        this.setState({
            nextTaskDate : e
        },()=>{
            let date1 = this.props.comingTomDate;
            let date2 = moment(this.state.nextTaskDate).format("YYYY-MM-DD");
            if(date1 >= date2){
                this.setState({
                    isDateValid : false
                })
            }else{
                this.setState({
                    isDateValid : true
                })
            }
        })

    }

    handleRowInput = (e) => {
        // console.log("element",e.target.name,e.target.value)
        let rowsLength = this.state.rows.length;
        console.log(rowsLength)

        let stateCopy = Object.assign({}, this.state);

        for(let i=0; i<rowsLength ; i++){
            console.log(",,,,,,,",e.target.name)
            if(e.target.name === `selectBox1-${i}`){
                stateCopy.data[i].time_spent_value = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `selectBox2-${i}`){
                stateCopy.data[i].est_time_value = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `task_name-${i}`){
                stateCopy.data[i].task_name = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `time_spent-${i}`){
                stateCopy.data[i].time_spent = parseInt(e.target.value);
                this.setState(stateCopy);
            }
            if(e.target.name === `est_time-${i}`){
                stateCopy.data[i].est_time = parseInt(e.target.value);
                this.setState(stateCopy);
            }
        }
    }

    handleAddRows = () => {

        let row = this.state.rows;
        let data = this.state.data;
        data.push({
            task_name: '',
            time_spent: 0,
            est_time: 0,
            time_spent_value : 'hour',
            est_time_value: 'hour',
            isCompleted: false
        })
        row.push('new row')
        this.setState({
            rows : row,
            data
        })
    }

    handleComplete = (index) => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.data[index].isCompleted = true
        this.setState(stateCopy);
    }

    handleInComplete = (index) => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.data[index].isCompleted = false
        this.setState(stateCopy);
    }

    handleDelete = (index) => {
        let data = [...this.state.data]; // make a separate copy of the array
        let rows = [...this.state.rows]

        if (index !== -1) {
            data.splice(index, 1);
            rows.splice(index,1)
            this.setState({data,
                            rows});
        }
        
    }

    handleCancel = () => {
        if(window.localStorage.getItem('userType') == 1){
            this.props.history.push({
                pathname: '/emp_dashboard',
                state : {
                    name : this.props.location.state.name,
                    user_id: this.props.location.state.user_id
                }
            })
        }
        if(window.localStorage.getItem('userType') == 2){
            this.props.history.push({
                pathname: '/dashboard'
            })
        }
    }

    handleSubmit = async () => {
        console.log("submit",coming_tomorrow)
        if(this.props.comingTom){
            coming_tomorrow = {
                status : true,
                till_date : ''
            }
        }else{
            coming_tomorrow = {
                status : false,
                till_date : this.props.comingTomDate
            }
        }

        if(this.state.selectOption === 'nextTaskYes'){
            let date = moment(this.state.nextTaskDate).format('YYYY-MM-DD')
            new_task = {
                for_tomorrow : true,
                completion_date : date,
                data : this.state.data
            }
        }else{
            new_task = {
                for_tomorrow : false,
                completion_date : '',
                data : []
            }
        }

        let addTaskData = {
            coming_tomorrow,
            new_task
        }

        if(window.localStorage.getItem('userType') == 1){
            addTaskData.user_id = this.props.location.state.user_id
        }

        let _r = await services.task.addNewTask(addTaskData);

        if(_r.success){
            toast.success("Successfully Added");

            if(window.localStorage.getItem('userType') == 1){
                this.props.history.push({
                    pathname: '/emp_dashboard',
                    state : {
                        name : this.props.location.state.name,
                        user_id: this.props.location.state.user_id
                    }
                })
            }
            if(window.localStorage.getItem('userType') == 2){
                this.props.history.push({
                    pathname: '/dashboard'
                })
            }
        }else{
            toast.error("Something Wrong")
        }

        console.log("submit",addTaskData)
    }

    render(){
        console.log("props",this.props,this.state)
        return (
            <div>
                <h3 className="ml-5">Do you have next task ?</h3>
                <div className="ml-5">
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="nextTaskYes" checked={this.state.selectOption === 'nextTaskYes'} />
                        <label className="ml-3 mt-3">Yes</label>
                    </span>
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="nextTaskNo" checked={this.state.selectOption === 'nextTaskNo'} />
                        <label className="ml-3 mt-3">No</label>
                    </span>
                </div>
                {
                    this.state.selectOption === 'nextTaskYes' && 
                    <div>
                        <div className="ml-5 mt-4">
                            <b className="ml-5">Completion Date :-</b>
                            <DatePicker
                                className="ml-3 text-center"
                                dateFormat="dd/MM/yyyy"
                                selected={this.state.nextTaskDate}
                                onChange={this.handleChange}
                                minDate={new Date()}
                            />
                            {
                                !this.state.isDateValid && 
                                <label className="text-danger ml-3">you are on leave on this {moment(this.state.nextTaskDate).format("DD-MM-YYYY")} date. </label> 
                            }
                        </div>
                        <div className="mt-5">
                            <ColoredLine color="#ADD8E6" />
                        </div>
                        <div className="ml-5 mt-4 mr-5">
                            {/* <TaskTableComponent/> */}
                            <h4>Tasks 
                                <img onClick={this.handleAddRows} className="ml-3" src={require('../../img/plus-sign-in-a-black-circle.png')} />
                            </h4>
                            <div>
                                <Table className="mt-5" striped bordered hover >
                                    <thead>
                                        <tr>
                                            {
                                                column.map((_c)=>(
                                                    <th className="text-center">{_c.name}</th>
                                                ))
                                            }
                                            <th className="text-center">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            this.state.rows.map((_r , index)=>(
                                                this.state.data[index].isCompleted ?
                                                <tr key={`row${index}`} className="text-center">
                                                    <td><input className="text-center" 
                                                            style={{textDecoration:"line-through"}}
                                                            onChange={this.handleRowInput} 
                                                            type="text" 
                                                            name={`task_name-${index}`} 
                                                            value={this.state.data[index].task_name}/>
                                                    </td>
                                                    <td>
                                                        <input className="text-center" 
                                                            style={{textDecoration:"line-through"}}
                                                            onChange={this.handleRowInput} 
                                                            type="number" 
                                                            name={`time_spent-${index}`} 
                                                            value={this.state.data[index].time_spent}/>
                                                        <select className="ml-2"
                                                                name={`selectBox1-${index}`}
                                                                onChange={this.handleRowInput}
                                                                value={this.state.data[index].time_spent_value}>
                                                            <option value='hour'>Hour</option>
                                                            <option value="min">Minute</option>
                                                        </select>
                                                    </td>
                                                    <td>
                                                        <input className="text-center"
                                                            style={{textDecoration:"line-through"}} 
                                                            onChange={this.handleRowInput} 
                                                            type="number" 
                                                            name={`est_time-${index}`} 
                                                            value={this.state.data[index].est_time}/>
                                                        <select className="ml-2"
                                                                name={`selectBox2-${index}`}
                                                                onChange={this.handleRowInput}
                                                                value={this.state.data[index].est_time_value}>
                                                            <option value='hour' >Hour</option>
                                                            <option value='min'>Minute</option>
                                                        </select>
                                                    </td>
                                                    <td>
                                                        <img onClick={()=>this.handleInComplete(index)} src={require("../../img/tick-1.png")} />
                                                        <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                    </td>
                                                </tr>
                                                :
                                                <tr key={`row${index}`} className="text-center">
                                                    <td><input className="text-center"
                                                            onChange={this.handleRowInput} 
                                                            type="text" 
                                                            name={`task_name-${index}`} 
                                                            value={this.state.data[index].task_name}/>
                                                    </td>
                                                    <td>
                                                        <input className="text-center" 
                                                            onChange={this.handleRowInput} 
                                                            type="number" 
                                                            name={`time_spent-${index}`} 
                                                            value={this.state.data[index].time_spent}/>
                                                        <select className="ml-2"
                                                                name={`selectBox1-${index}`}
                                                                onChange={this.handleRowInput}
                                                                value={this.state.data[index].time_spent_value}>
                                                            <option value='hour'>Hour</option>
                                                            <option value="min">Minute</option>
                                                        </select>
                                                    </td>
                                                    <td>
                                                        <input className="text-center" 
                                                            onChange={this.handleRowInput} 
                                                            type="number" 
                                                            name={`est_time-${index}`} 
                                                            value={this.state.data[index].est_time}/>
                                                        <select className="ml-2"
                                                                name={`selectBox2-${index}`}
                                                                onChange={this.handleRowInput}
                                                                value={this.state.data[index].est_time_value}>
                                                            <option value='hour' >Hour</option>
                                                            <option value='min'>Minute</option>
                                                        </select>
                                                    </td>
                                                    <td>
                                                        <img onClick={()=>this.handleComplete(index)} src={require("../../img/tick.png")} />
                                                        <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                    </td>
                                                </tr>
                                                )
                                            )
                                        }
                                    </tbody>
                                </Table>
                            </div>
                        </div>
                    </div>
                }
                <div className="text-center mt-5 mb-5">
                    <Button className="mr-4" type="button" onClick={this.handleSubmit} variant="outline-primary">Submit</Button>
                    <Button className="ml-4" variant="outline-secondary" onClick={this.handleCancel} type="button">Cancel</Button>
                </div>
            </div>
        )
    }
}

export default withRouter(NextTaskStatus);