import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Form } from 'react-bootstrap';
import {Link, withRouter} from 'react-router-dom'
import { toast } from 'react-toastify'
import services from '../services';
// import 'react-toastify/dist/ReactToastify.css';
// import backgroundImage from './logHour.jpg'
// import Login from './Login/login'

class Login extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      email : '',
      password : '',
    }
  }
  
  handleFields = (e) => {
    if(e.target.name == 'email'){
      this.setState({
        email : e.target.value
      })
    }
    if(e.target.name == 'password'){
      this.setState({
        password : e.target.value
      })
    }
  }

  handleLogin = async () => {
    let reg = /^([A-Za-z0-9_\-\.])+\@enukesoftware.com$/;

    if(this.state.email === '' || this.state.password === ''){
        toast.error("Email and Password Required");
    }else if (this.state.email !== '' && !reg.test(this.state.email)) {
        toast.error("Enter Correct Email")
    }else{
        let _r = await services.auth.login(this.state.email,this.state.password);
        if(_r.success){
          window.localStorage.setItem('username',_r.data.name);
          window.localStorage.setItem('token',_r.data.token);
          window.localStorage.setItem('userType',_r.data.role_id)
          console.log(".........",_r.data)
          toast.success('Successfully Login')
          window.location = '/'
        }else{
          toast.error("Invalid Credentials")
        }
    }
  }

  render(){
    return (
      <div>
        <Form> 
        <Form.Group controlId="formBasicEmail">
          <Form.Label>Email</Form.Label>
          <Form.Control type="email" name='email' value={this.state.email} onChange={this.handleFields} placeholder="Enter email" />
        </Form.Group>
        <Form.Group controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control type="password" required name='password' value={this.state.password} onChange={this.handleFields} placeholder="Password" />
        </Form.Group>
        <Button variant="primary" onClick={this.handleLogin} type="button">
          Login
        </Button>
        <div className="float-right mt-2">
          <Link to={'/signup'}>SignUp</Link>
          <span>/</span>
          <Link to={'/forgot_password'}>ForgotPassword?</Link>
        </div>
      </Form>
      </div>
    )
  }
}

export default withRouter(Login);
