import React from 'react';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ColoredLine from '../common/horizontalLine'
import { Table ,Button } from 'react-bootstrap';
import {withRouter} from 'react-router-dom'
import services from '../../services';
import { toast } from 'react-toastify';
// import TaskTableComponent from '../AddTask/taskTable'
var moment = require('moment');


const column = [
    {
        name: "Task",
        key: "task"
    },
    {
        name: "Time Spent",
        key: "time_spent"
    },
    {
        name: "Estimated Time",
        key: "est_time"
    }
]
let coming_tomorrow = {}
let new_task = {}

class EditNextTask extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            selectOption : 'nextTaskYes',
            nextTaskDate : new Date(),
            rows : ['row'],
            // data : [
            //     {
            //         task_name: '',
            //         time_spent: 0,
            //         est_time: 0,
            //         time_spent_value : 'hour',
            //         est_time_value: 'hour',
            //         isCompleted : false,
            //     }
            // ],
            editData : [],
            selectedIds : [],
            data : []
        }
    }

    componentDidMount() {
        let date = new Date();
        if(this.props.props.location.state.type === 'tomorrow'){
            let tomorrow = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 1));
            this.setState({
                nextTaskDate: tomorrow,
                editData : this.props.data,
                selectedIds : []
            })
        }else if(this.props.props.location.state.type === 'upcoming'){
            let upcoming = new Date(date.getFullYear(), date.getMonth(), (date.getDate() + 2));
            this.setState({
                nextTaskDate: upcoming,
                editData : this.props.data,
                selectedIds : []
            })
        }else{
            let today = this.state.nextTaskDate;
            this.setState({
                nextTaskDate: today,
                editData : this.props.data,
                selectedIds : []
            })
        }
    }


    handleRadio = (e) => {
        console.log(".,.,.,.,",e.target.value)
        this.setState({
            selectOption : e.target.value,
            selectedIds : []
        })
    }

    handleChange =(e) => {
        // console.log("Date",e)
        this.setState({
            nextTaskDate : e
        })

    }

    componentDidUpdate(prevProps,newProps){
        // console.log("Update",JSON.stringify(prevProps.data),JSON.stringify(newProps.editData))
        if(JSON.stringify(prevProps.data)!==JSON.stringify(newProps.editData)){
            this.setState({
                editData : prevProps.data
            })
        }
    }

    handleRowInput = (e) => {
        // console.log("element",e.target.name,e.target.value)
        let rowsLength = this.state.editData.length;
        console.log(rowsLength)

        let stateCopy = Object.assign({}, this.state);

        for(let i=0; i<rowsLength ; i++){
            console.log(",,,,,,,",e.target.name)
            if(e.target.name === `selectBox1-${i}`){
                stateCopy.editData[i].time_spent_value = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `selectBox2-${i}`){
                stateCopy.editData[i].estimated_time_value = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `task_name-${i}`){
                stateCopy.editData[i].task_name = e.target.value;
                this.setState(stateCopy);
            }
            if(e.target.name === `time_spent-${i}`){
                stateCopy.editData[i].time_spent = parseInt(e.target.value);
                this.setState(stateCopy);
            }
            if(e.target.name === `est_time-${i}`){
                stateCopy.editData[i].estimated_time = parseInt(e.target.value);
                this.setState(stateCopy);
            }
        }
    }

    handleComplete = (index) => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.editData[index].completed = 1
        this.setState(stateCopy);
    }

    handleInComplete = (index) => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.editData[index].completed = 0
        this.setState(stateCopy);
    }

    handleDelete = (index) => {

        console.log('hhhhhhhhh',index)
        let stateCopy = Object.assign({}, this.state);
        stateCopy.editData[index].deleted = 1
        this.setState(stateCopy);
        
    }

    handleCancel = () => {
        this.props.history.push({
            pathname : "/dashboard"
        })
    }

    handleCheckbox = (e) => {
        console.log("..",e.target.name,e.target.checked)
        let ids = [...this.state.selectedIds];
        if(e.target.name === 'Bulk_Check'){
            ids = []
            if(e.target.checked){
                this.state.editData.map((_r)=>{
                    if(!ids.includes(_r.id)>-1){
                        ids.push(_r.id+'')
                    }
                })
                this.setState({
                    selectedIds : ids
                })
            }else{
                this.setState({
                    selectedIds : []
                })
            }
        }
        if(e.target.name === 'single_check'){
            console.log("kkkkdddd",!this.state.selectedIds.includes(e.target.value))
            if(!this.state.selectedIds.includes(e.target.value)){
                ids.push(e.target.value)
            }else{
                let index = ids.findIndex(i=> i===e.target.value);
                if(index >= 0){
                    ids.splice(index,1);
                }
            }
            this.setState({
                selectedIds : ids
            })
        }
    }

    handleSubmit = async () => {
        
        if(this.props.selectedOption !== ''){
            let tomorrowDate = moment(this.props.comingTomDate).format('YYYY-MM-DD')
            // console.log("submit",this.state.selectedIds,this.props.props.location.state.type,tomorrowDate)
            if(this.props.comingTom){
                coming_tomorrow = {
                    status : true,
                    till_date : ''
                }
            }else{
                coming_tomorrow = {
                    status : false,
                    till_date : tomorrowDate
                }
            }

            if(this.state.selectOption === 'nextTaskYes'){
                if(this.state.selectedIds.length > 0){
                    let data = [];
                    for(let i=0;i<this.state.selectedIds.length; i++){
                        // console.log("filter",this.state.editData.filter(row => row.id == this.state.selectedIds[i]))
                        let row = this.state.editData.map(row => {
                            if(row.id == this.state.selectedIds[i]){
                                data.push(row);
                            }
                        });
                    }
                    new_task = {
                        for_tomorrow : true,
                        completion_date : moment(this.state.nextTaskDate).format('YYYY-MM-DD'),
                        data : data
                    }
                }else{
                    toast.error('Please select Tasks.')
                }
            }else{
                new_task = {
                    for_tomorrow : false,
                    completion_date : '',
                    data : this.state.editData
                }
            }
            
            let editTaskData = {
                coming_tomorrow,
                new_task
            }

            if(editTaskData && editTaskData.coming_tomorrow && editTaskData.new_task){
                let _r = await services.task.postEditTask(this.props.props.location.state.type,editTaskData);
                if(_r.success){
                    toast.success("Successfully Updated");
                    this.props.history.push({
                        pathname: '/dashboard'
                    })
                }
            }
        console.log("submit",editTaskData)
        }else{
            toast.error("Please Select Coming Tomorrow Date.")
        }
    }

    render(){
        console.log("props",this.props)
        return (
            <div>
                <h3 className="ml-5">Want to change Schedule ?</h3>
                <div className="ml-5">
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="nextTaskYes" checked={this.state.selectOption === 'nextTaskYes'} />
                        <label className="ml-2">Yes</label>
                    </span>
                    <span class="radio-inline">
                        <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="nextTaskNo" checked={this.state.selectOption === 'nextTaskNo'} />
                        <label className="ml-2">No</label>
                    </span>
                </div>
                {
                    this.state.selectOption === 'nextTaskYes' && 
                    <div>
                        <div className="ml-5 text-black-50">
                            <img className="ml-5" src={require('../../img/attention.png')} />
                            <label className="ml-2">To reschedule tasks, select the tasks below and press submit.</label>
                        </div>
                        <div className="ml-5 mt-4">
                            <b className="ml-5">Completion Date :-</b>
                            <DatePicker
                                className="ml-3 text-center"
                                dateFormat="dd/MM/yyyy"
                                selected={this.state.nextTaskDate}
                                onChange={this.handleChange}
                                minDate={new Date()}
                            />
                        </div>
                        </div>
                    }
                        <div className="mt-5">
                            <ColoredLine color="#ADD8E6" />
                        </div>
                        <div className="ml-5 mt-4 mr-5">
                            {/* <TaskTableComponent/> */}
                            <h4>Tasks 
                                {/* <img className="ml-3" src={require('../../img/plus-sign-in-a-black-circle.png')} /> */}
                            </h4>
                            <div>
                                <Table className="mt-5" striped bordered hover >
                                    <thead>
                                        <tr>
                                            {
                                                this.state.selectOption === 'nextTaskYes' &&
                                                <th className="z-large text-center">
                                                <input
                                                style={{width:20,height:20}}
                                                type = 'checkbox'
                                                checked = {this.state.selectedIds.length === this.state.editData.length ? true : false}
                                                name = "Bulk_Check"
                                                onChange = {this.handleCheckbox}
                                                />
                                                </th>
                                            }
                                            {
                                                column.map((_c)=>(
                                                    <th className="text-center">{_c.name}</th>
                                                ))
                                            }
                                            <th className="text-center">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            this.state.editData.map((_r , index)=>(
                                                _r && !_r.deleted &&
                                                (this.state.selectOption === 'nextTaskYes' ?
                                                    _r.completed == '1' ?
                                                    (
                                                        this.state.selectedIds.includes(_r.id+'') ?
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td>
                                                                    <input
                                                                    style={{width:20,height:20}}
                                                                    type = 'checkbox'
                                                                    value={_r.id+''}
                                                                    checked = {this.state.selectedIds.includes(_r.id + '')  ? true : false}
                                                                    name = "single_check"
                                                                    onChange = {this.handleCheckbox}
                                                                    />
                                                                </td>
                                                                <td><input className="text-center" 
                                                                        style={{textDecoration:"line-through"}}
                                                                        onChange={this.handleRowInput} 
                                                                        type="text" 
                                                                        name={`task_name-${index}`} 
                                                                        value={this.state.editData[index].task_name}/>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center" 
                                                                        style={{textDecoration:"line-through"}}
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`time_spent-${index}`} 
                                                                        value={this.state.editData[index].time_spent}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox1-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].time_spent_value}>
                                                                        <option value='hour'>Hour</option>
                                                                        <option value="min">Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center"
                                                                        style={{textDecoration:"line-through"}} 
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`est_time-${index}`} 
                                                                        value={this.state.editData[index].estimated_time}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox2-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].estimated_time_value}>
                                                                        <option value='hour' >Hour</option>
                                                                        <option value='min'>Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <img onClick={()=>this.handleInComplete(index)} src={require("../../img/tick-1.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                        )
                                                        :
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td>
                                                                    <input
                                                                    style={{width:20,height:20}}
                                                                    type = 'checkbox'
                                                                    value={_r.id+''}
                                                                    checked = {this.state.selectedIds.includes(_r.id + '')  ? true : false}
                                                                    name = "single_check"
                                                                    onChange = {this.handleCheckbox}
                                                                    />
                                                                </td>
                                                                <td style={{textDecoration:"line-through"}}>{_r.task_name}</td>
                                                                <td style={{textDecoration:"line-through"}}>{`${_r.time_spent}  ${_r.time_spent_value}`}</td>
                                                                <td style={{textDecoration:"line-through"}}>{`${_r.estimated_time}  ${_r.estimated_time_value}`}</td>
                                                                <td>
                                                                    <img onClick={()=>this.handleInComplete(index)} src={require("../../img/tick-1.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                        )
                                                    )
                                                    :
                                                        this.state.selectedIds.includes(_r.id+'') ?
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td>
                                                                    <input
                                                                    style={{width:20,height:20}}
                                                                    type = 'checkbox'
                                                                    value={_r.id+''}
                                                                    checked = {this.state.selectedIds.includes(_r.id + '')  ? true : false}
                                                                    name = "single_check"
                                                                    onChange = {this.handleCheckbox}
                                                                    />
                                                                </td>
                                                                <td><input className="text-center"
                                                                        onChange={this.handleRowInput} 
                                                                        type="text" 
                                                                        name={`task_name-${index}`} 
                                                                        value={this.state.editData[index].task_name}/>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center"
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`time_spent-${index}`} 
                                                                        value={this.state.editData[index].time_spent}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox1-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].time_spent_value}>
                                                                        <option value='hour'>Hour</option>
                                                                        <option value="min">Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center"
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`est_time-${index}`} 
                                                                        value={this.state.editData[index].estimated_time}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox2-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].estimated_time_value}>
                                                                        <option value='hour' >Hour</option>
                                                                        <option value='min'>Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <img onClick={()=>this.handleComplete(index)} src={require("../../img/tick.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                        )
                                                        :
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td>
                                                                    <input
                                                                    style={{width:20,height:20}}
                                                                    type = 'checkbox'
                                                                    value={_r.id+''}
                                                                    checked = {this.state.selectedIds.includes(_r.id + '')  ? true : false}
                                                                    name = "single_check"
                                                                    onChange = {this.handleCheckbox}
                                                                    />
                                                                </td>
                                                                <td>{_r.task_name}</td>
                                                                <td>{`${_r.time_spent}  ${_r.time_spent_value}`}</td>
                                                                <td>{`${_r.estimated_time}  ${_r.estimated_time_value}`}</td>
                                                                <td>
                                                                    <img onClick={()=>this.handleComplete(index)} src={require("../../img/tick.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                        )
                                                :
                                                _r.completed == '1' ?
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td><input className="text-center" 
                                                                        style={{textDecoration:"line-through"}}
                                                                        onChange={this.handleRowInput} 
                                                                        type="text" 
                                                                        name={`task_name-${index}`} 
                                                                        value={this.state.editData[index].task_name}/>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center" 
                                                                        style={{textDecoration:"line-through"}}
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`time_spent-${index}`} 
                                                                        value={this.state.editData[index].time_spent}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox1-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].time_spent_value}>
                                                                        <option value='hour'>Hour</option>
                                                                        <option value="min">Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center"
                                                                        style={{textDecoration:"line-through"}} 
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`est_time-${index}`} 
                                                                        value={this.state.editData[index].estimated_time}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox2-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].estimated_time_value}>
                                                                        <option value='hour' >Hour</option>
                                                                        <option value='min'>Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <img onClick={()=>this.handleInComplete(index)} src={require("../../img/tick-1.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                        )
                                                        :
                                                        (
                                                            <tr key={`row${index}`} className="text-center">
                                                                <td><input className="text-center" 
                                                                        onChange={this.handleRowInput} 
                                                                        type="text" 
                                                                        name={`task_name-${index}`} 
                                                                        value={this.state.editData[index].task_name}/>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center"
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`time_spent-${index}`} 
                                                                        value={this.state.editData[index].time_spent}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox1-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].time_spent_value}>
                                                                        <option value='hour'>Hour</option>
                                                                        <option value="min">Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <input className="text-center" 
                                                                        onChange={this.handleRowInput} 
                                                                        type="number" 
                                                                        name={`est_time-${index}`} 
                                                                        value={this.state.editData[index].estimated_time}/>
                                                                    <select className="ml-2"
                                                                            name={`selectBox2-${index}`}
                                                                            onChange={this.handleRowInput}
                                                                            value={this.state.editData[index].estimated_time_value}>
                                                                        <option value='hour' >Hour</option>
                                                                        <option value='min'>Minute</option>
                                                                    </select>
                                                                </td>
                                                                <td>
                                                                    <img onClick={()=>this.handleComplete(index)} src={require("../../img/tick.png")} />
                                                                    <img className="float-right" onClick={()=>this.handleDelete(index)} src={require('../../img/trash-2.png')} />
                                                                </td>
                                                            </tr>
                                                    )
                                                )
                                                )
                                            )
                                        }
                                    </tbody>
                                </Table>
                            </div>
                        </div>
                <div className="text-center mt-5 mb-5">
                    <Button className="mr-4" type="button" onClick={this.handleSubmit} variant="outline-primary">Submit</Button>
                    <Button className="ml-4" variant="outline-secondary" onClick={this.handleCancel} type="button">Cancel</Button>
                </div>
            </div>
        )
    }
}

export default withRouter(EditNextTask);